body {
  background: #f5f5f5; }

main {
  padding: 20px 0; }

#contentjqxgrid > span:last-of-type {
  display: none !important; }

input.error {
  border: 1px solid red; }

.hidden {
  display: none; }

.error-hide + label.error {
  display: none !important; }

.w-50p {
  width: 50px; }

.w-100p {
  width: 100px; }

.w-300p {
  width: 300px; }

.uk-width-1-12 {
  width: calc(100% * 1 / 12.001); }

@media screen and (max-width: 1200px) {
  .uk-width-1-12 {
    width: calc(100% * 1 / 7.001); } }

.uk-padding-left-5 {
  padding-left: 5px !important; }

.uk-padding-right-5 {
  padding-right: 5px !important; }

.v-align-middle {
  vertical-align: middle; }

.jqx_cell_disabled input[type="button"] {
  display: none; }

.jqx_cell_bgsuccess {
  background: #d4edda; }

.jqx_cell_bgdanger {
  background: #f8d7da; }

.jqxMenu-topmenu {
  width: 100%; }

.info-heading-block {
  background-color: #e2e2e2;
  padding: 20px 0;
  margin-bottom: 20px; }

.info-heading-block label {
    font-weight: bold;
    margin-right: 10px;
    margin-bottom: 0; }

.sessionForm {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

/*SESSION INFO BLOCK*/

.sessioninfo-block .col-auto {
  /*padding: 0;*/
  width: 130px; }

.sessioninfo-block label {
  background: #353434;
  color: #fff;
  text-align: center;
  display: block;
  padding: 5px 10px;
  font-weight: normal;
  margin-right: 0;
  font-size: 14px; }

.sessioninfo-block .val {
  text-align: center; }

/* HOEMPAGE*/

.pageHome {
  font-family: 'Raleway', sans-serif;
  color: #636b6f;
  background-color: #fff;
  font-weight: 100;
  height: 100vh;
  margin: 0;
  margin: -20px; }

.full-height {
  height: 100vh; }

.flex-center {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center; }

.position-ref {
  position: relative; }

.top-right {
  position: absolute;
  right: 10px;
  top: 18px; }

.content {
  text-align: center; }

.title {
  font-size: 84px; }

.links > a {
  color: #636b6f;
  padding: 0 25px;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: .1rem;
  text-decoration: none;
  text-transform: uppercase; }

.m-b-md {
  margin-bottom: 30px; }
